@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT-Mono";
  font-display: swap;
  src: local("PT-Mono"), url("../../fonts/PTMono-Regular.ttf"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto-Medium";
  font-display: swap;
  src: local("Roboto-Medium"), url("../../fonts/Roboto-Medium.ttf"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto-Condensed-Regular";
  font-display: swap;
  src: local("Roboto-Condensed-Regular"), url("../../fonts/RobotoCondensed-Regular.ttf"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto-Condensed-Bold";
  font-display: swap;
  src: local("Roboto-Condensed-Bold"), url("../../fonts/RobotoCondensed-Bold.ttf"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

.page,
.page-body {
  margin: 0;
  padding: 0; }

.page {
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  font-family: "PT-Mono", "Arial", sans-serif;
  color: #f2f5f7;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

.page-body {
  height: 100%;
  display: grid;
  grid-template-rows: 1fr min-content;
  align-content: start;
  background-color: #f8fdff; }

a {
  color: #f2f5f7;
  text-decoration: none; }

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type='number'] {
  appearance: textfield; }

select,
textarea,
input:matches([type='email'], [type='number'], [type='password'], [type='search'], [type='tel'], [type='text'], [type='url']) {
  appearance: none; }

button,
[type='button'],
[type='reset'],
[type='submit'] {
  appearance: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.wrapper {
  width: 100%;
  max-width: 1222px;
  margin: 0 auto; }

.page-header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  padding: 25px 0 30px 0;
  width: 100%;
  position: fixed; }
  @media (max-width: 1221px) {
    .page-header {
      padding: 20px 70px 25px 50px; } }
  @media (max-width: 769px) {
    .page-header {
      padding: 15px 36px 20px 26px; } }

.main-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1160px;
  column-gap: 20px; }

.main-nav__logo-link {
  display: flex;
  align-items: center;
  column-gap: 15px;
  margin-right: 7px; }

.main-nav__logo-link svg {
  width: 55px;
  height: 55px; }
  @media (max-width: 769px) {
    .main-nav__logo-link svg {
      width: 35px;
      height: 35px; } }

.main-nav__logo-link p {
  display: flex;
  flex-direction: column;
  white-space: nowrap;
  row-gap: 2px;
  font-family: "Roboto-Condensed-Bold", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px; }
  @media (max-width: 769px) {
    .main-nav__logo-link p {
      display: none; } }

.main-nav__logo-link span {
  font-family: "Roboto-medium", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px; }

.main-nav__contacts-wrapper {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  column-gap: 120px;
  row-gap: 20px; }
  @media (max-width: 1221px) {
    .main-nav__contacts-wrapper {
      column-gap: 40px;
      align-self: flex-start;
      padding-top: 10px;
      padding-right: 10px; } }
  @media (max-width: 769px) {
    .main-nav__contacts-wrapper {
      padding-top: 5px; } }

.main-nav__contacts-wrapper svg {
  width: 20px;
  height: 20px; }

.main-nav__contacts-link {
  display: flex;
  align-items: center;
  column-gap: 15px; }

@media (max-width: 1221px) {
  .main-nav__contacts-wrapper p,
  .main-nav__button {
    display: none; } }

.main-nav__button {
  white-space: nowrap;
  background-color: inherit;
  border: 1px solid #f2f5f7;
  padding: 10px 22px; }

.button {
  display: inline-block;
  color: #f2f5f7;
  font-family: "Roboto-Condensed-Bold", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  text-transform: uppercase;
  border-radius: 3px; }

.modal {
  display: none;
  position: fixed;
  top: 146px;
  left: 50%;
  transform: translateX(-50%);
  padding: 40px;
  background-color: #00b3ff;
  box-sizing: inherit;
  width: 500px; }
  .modal--isOpen {
    display: block; }

.modal__close-button {
  cursor: pointer;
  position: absolute;
  width: 27px;
  height: 27px;
  top: 25px;
  right: 25px; }
  .modal__close-button::before, .modal__close-button::after {
    content: "";
    position: absolute;
    left: 50%;
    height: 100%;
    width: 1px;
    background-color: #ffffff; }
  .modal__close-button::before {
    transform: rotate(45deg); }
  .modal__close-button::after {
    transform: rotate(-45deg); }

.modal h2 {
  font-size: 30px;
  font-family: "Roboto-Condensed-Regular", "Arial", sans-serif;
  margin-bottom: 15px; }

.modal p {
  line-height: 22px;
  margin-bottom: 40px; }

.modal form {
  display: flex;
  flex-direction: column;
  row-gap: 20px; }

form input,
form textarea {
  background: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px; }

form {
  position: relative; }

form input,
form textarea,
form input::placeholder,
form textarea::placeholder {
  font-family: "PT-Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff; }

form input {
  padding: 15px; }

form textarea {
  padding: 25px 15px; }

form label {
  font-family: "PT-Mono", "Arial", sans-serif;
  font-size: 14px; }

form textarea {
  margin-bottom: 5px; }

form input[type="checkbox"] {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0; }

form label[for="checkbox"] {
  position: relative;
  padding-left: 25px;
  cursor: pointer; }
  form label[for="checkbox"]::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 15px;
    height: 15px;
    background: url("../img/icons/checkbox.svg") 50% 50% no-repeat; }

form input[type="checkbox"]:checked + label[for="checkbox"]::after {
  content: "";
  position: absolute;
  z-index: 100;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  background: url("../../img/icons/checkbox-confirm.svg") 50% 50% no-repeat; }

form input[type="submit"] {
  background-color: #041827;
  padding: 15px 60px;
  text-transform: uppercase; }

.overlay {
  display: none;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100vh;
  background: rgba(3, 16, 27, 0.7); }
  .overlay--isShow {
    display: block; }

.page-footer {
  background-color: #041827; }
